import React, { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import Profile from "./Profile";
import Top from "./Top";
import Bottom from "./Bottom";
import AboutContain from "./contain/AboutContain";
import Container from "./Container";
import CareerContain from "./contain/CareerContain";
import ProjectContain from "./contain/ProjectContain";
import SocialContain from "./contain/SocialContain";

const Outside = styled.div`
    width: 100vw;
    height: 100vh;
    background-color: ${({theme}) => theme.darkTheme.bg_l1};
    & *{
        color: ${({theme}) => theme.darkTheme.color};
    }
`;
const Main = styled.div`
    font-size: 4vw !important;
    width: 100vw;
    height: calc(100vh - 30vw);
    padding: 1vh;
    overflow: hidden;
    
    & *{
        border-width: 0.1px;
    }
    
    & > div{
        border-radius: 1.5vh;
    }
    
    & > *+*{
        margin-top: 1vh;
    }
`;

function Wrap(props) {
    const mainRef = useRef();
    const [menu, setMenu] = useState(0);

    const scrollToMenu = [
        [0, 0], // Profile(50vw)
        //[0.5, 0.011], // AboutContain(53vw)
        [1.03, 0.02], // CareerContain(98vh-30vw)
        [0.73, 1.01], // ProjectContain(98vh-30vw)
        [0.43, 2.00], // 
    ];

    useEffect(()=>{
        const mainElement = mainRef.current;
        const vW = window.innerWidth;
        const vH = window.innerHeight;
        if (mainElement) {
            mainElement.scrollTo({
                top: scrollToMenu[menu][0] * vW + scrollToMenu[menu][1] * vH,
                behavior: 'smooth'
            });
        }
    },[menu]);

    const containerRefs = [
        useRef(), useRef()
    ];
    const containerScrollTop = () => {
        containerRefs.map((containerRef)=>{
            const containerElement = containerRef.current;
            if (containerElement) {
                containerElement.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });
            }
        });
    }
    useEffect(()=>{
        containerScrollTop();
    }, [menu]);

    const projectScrollTop = () => {
        const projectElement = containerRefs[1].current;
        if (projectElement) {
            projectElement.scrollTop = 0;
        }
    }

    const [projectReset, setProjectReset] = useState(true);

    return (
        <Outside>
            <Top darkModeToggle={props.darkModeToggle}/>
            <Main ref={mainRef}>
                <Profile />
                <AboutContain/>
                <Container ref={containerRefs[0]} canScroll={menu===1}>
                    <CareerContain />
                </Container>
                <Container ref={containerRefs[1]} canScroll={menu===2}>
                    <ProjectContain
                        projectReset={projectReset}
                        setProjectReset={setProjectReset}
                        projectScrollTop={projectScrollTop}
                    />
                </Container>
                <Container>
                    <SocialContain />
                </Container>
            </Main>
            <Bottom
                setProjectReset={setProjectReset}
                setMenu={setMenu}
            />
        </Outside>
    );
}

export default Wrap;