import React, { forwardRef } from "react";
import styled, { css } from "styled-components";

const Main = styled.div`
    width: 100%;
    height: calc(98vh - 30vw);
    background-color: ${({theme}) => theme.darkTheme.bg1};
    padding: 7vw 5vw;
    border-radius: 0;
    position: relative;

    ${props => props.canScroll ? css`
        overflow-y: scroll;
        &::-webkit-scrollbar {
            width: 0;
            display: none;
        }
    ` : css`
        overflow: hidden;
    `};
    
`;



function Container(props, ref) {

    return(
        <Main ref={ref} canScroll={props.canScroll}>
            {props.children}
        </Main>
    );
}

export default forwardRef(Container);